import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-article-list',
	templateUrl: './article-list.component.html',
	styleUrls: ['./article-list.component.css']
})

export class ArticleListComponent implements OnInit {

	//input 顯示序號
	@Input() input_param;


	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;

    	//最愛或全部
    	if(this.input_param.favorite==1)
    		this.getFavoriteArticles();
    	else
    		this.getArticleList();
	}

	
	list;
	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 2;
	moredata = 1;

	constructor(private http:HttpService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		
		//最愛或全部
    	if(this.input_param.favorite==1)
    		this.getFavoriteArticles();
    	else
    		this.getArticleList();

	}


	//output for love. unfavorite => remove 
	onListenChild(event: any, aid){
		$(`#${aid}`).remove();
	}


	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}


	//取得最愛文章列表
	getFavoriteArticles(){
		let url:string;

		const paramObject = {
			'from': this.input_param.from,
			'limit':this.limit,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/content/article/favorite?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.articles.length==0){
					console.log("empty array");
					this.moredata = 0;
				}

				this.list = res.articles;
				console.log(res);
			}
		);
	}


	//取得所有文章測驗列表
	getArticleList() {
		let url:string;

		const paramObject = {
			'website_id': 1,
			'category_id': this.input_param.category_id,
			'sort': this.input_param.sort,
			'sort_order': this.input_param.sort_order,
			'year': this.input_param.year,
			'from': this.input_param.from,
			'limit':this.limit,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/content/article?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.articles.length==0){
					console.log("empty array");
					this.moredata = 0;
				}

				this.list = res.articles;
				console.log(res);
			}
		);

	}

}
