import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { WriterService } from "../writer.service";
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-writer-activity',
	templateUrl: './writer-activity.component.html',
	styleUrls: ['./writer-activity.component.css']
})
export class WriterActivityComponent implements OnInit {

	//input 顯示序號
	@Input() input_param;

	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 2;
	moredata = 1;
	list;

	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;
    	this.getWriterProduct(this.input_param.id, this.input_param.type,this.input_param.from,this.limit);
    	//this.writerService.getWriterArticle(this.input_param.id);
	}

	constructor(public writerService:WriterService, private httpService:HttpService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		this.getWriterProduct(this.input_param.id, this.input_param.type,this.input_param.from,this.limit);
		//this.writerService.getWriterArticle(this.input_param.id);
	}

	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}


	//作者相關商品
	getWriterProduct(writer_id, product_type, from, limit){
		let paramObject = {
			'id':writer_id,
			'type':product_type,
			'from':from,
			'limit':limit,
			'sort':'post_date',
			'sort_order':'desc',
		};
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/writer/products?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.products.length==0){
					console.log("empty array");
					this.moredata = 0;
					return;
				}

				console.log(res);
				this.list = res.products;
			}
		);
	}

}
