import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { WriterService } from "../writer.service";
import  * as Config from '../../models/config.model';
import { WriterActivityComponent } from "../writer-activity/writer-activity.component";
import { HttpService } from "../../services/http.service";

@Component({
	selector: 'app-writer-article',
	templateUrl: './writer-article.component.html',
	styleUrls: ['./writer-article.component.css']
})
export class WriterArticleComponent implements OnInit {

	//input 顯示序號
	@Input() input_param;

	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 2;
	moredata = 1;
	articles;

	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
			this.input_param = changes.input_param.currentValue;
			this.readmoreCheck = 0;
			this.getWriterArticle(this.input_param.id, this.input_param.from, this.limit);
			//this.writerService.getWriterArticle(this.input_param.id);
	}

	constructor(public writerService:WriterService, private httpService:HttpService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		this.getWriterArticle(this.input_param.id, this.input_param.from, this.limit);
		//this.writerService.getWriterArticle(this.input_param.id);
	}

	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}


	//作者相關文章
	getWriterArticle(writer_id, from, limit){
		let paramObject = {
			'id':writer_id,
			'from':from,
			'limit':limit
		};
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/writer/articles?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.articles.length==0){
					console.log("empty array");
					this.moredata = 0;
					return;
				}
				console.warn(res);
				this.articles = res.articles;
			}
		);
	}

}
