import { Component, OnInit } from '@angular/core';
import { LayoutService } from "../../services/layout.service";

@Component({
	selector: 'app-writer-home',
	templateUrl: './writer-home.component.html',
	styleUrls: ['./writer-home.component.css']
})
export class WriterHomeComponent implements OnInit {

	input_param; 
	sortType = 'created_at';
	order = 'desc';
	limit = 2;

	constructor(public layoutService:LayoutService) { }

	ngOnInit() {
		this.layoutService.getSummary('page_writer_introduction');
		this.input_param = {
			website_id: 1,
			sort: this.sortType,
			sort_order: this.order,
			from: 0,
		};
	}


	//sort 一般篩選器
	sort($type,$order){
		this.sortType = $type;
		this.order = $order;
		this.input_param = {
			website_id: 1,
			sort: $type,
			sort_order: $order,
			from: 0,
		};
	}




}
