import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ArticleHomeComponent } from "../article/article-home/article-home.component";
import { ArticleReadComponent } from "../article/article-read/article-read.component";
import { WriterHomeComponent } from "./writer-home/writer-home.component";
import { WriterInfoComponent } from "./writer-info/writer-info.component";


const routes: Routes = [

	//作者
	{ path: '', component: WriterHomeComponent },
	{ path: 'read/:id', component: WriterInfoComponent },


	//文章
	{ path: 'article', component: ArticleHomeComponent },
	{ path: 'article/read/:id', component: ArticleReadComponent },


];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WriterRoutingModule { }
