import { Component, OnInit } from '@angular/core';
import { WriterService } from "../writer.service";
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import * as Config from "../../models/config.model";

@Component({
	selector: 'app-writer-info',
	templateUrl: './writer-info.component.html',
	styleUrls: ['./writer-info.component.css']
})
export class WriterInfoComponent implements OnInit {

	fileUrl = Config.FileUrl;
	input_activity;
	input_course;
	input_ebook;
	input_article;
	input_psychology;

	constructor(public writerService:WriterService, private route:ActivatedRoute) { }

	ngOnInit() {
		this.route.params.subscribe(params => {
			this.writerService.getWriterInfo(params['id']);
			this.input_activity = {
				'id': params['id'],
				'type': 'activity',
				'from':0,
			};

			this.input_course = {
				'id': params['id'],
				'type': 'course',
				'from':0,
			};

			this.input_ebook = {
				'id': params['id'],
				'type': 'e-book',
				'from':0,
			};

			this.input_article = {
				'id': params['id'],
				'from':0,
			};

			this.input_psychology = {
				'id': params['id'],
				'from':0,
			};
		});
	}

}
