import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-article-favorite',
	templateUrl: './article-favorite.component.html',
	styleUrls: ['./article-favorite.component.scss']
})
export class ArticleFavoriteComponent implements OnInit {

	@Input() favorite_status;
	@Input() input_aid;
	@Output() childEvent = new EventEmitter<any>(); //提供Output裝飾器，讓該事件成為父親模板的事件

	constructor(private httpService:HttpService) { }

	ngOnInit() {
	}


	//取消或加入收藏
	reversLove(){

		//已收藏 取消
		if(this.favorite_status==1){
			this.cancel_favorite();
		//未收藏 加入
		}else{			
			this.add_favorite();
		}

	}


	//加入收藏
	add_favorite(){
		let params:FormData = new FormData();
		params.append('id', this.input_aid);
		let url = `${Config.APIUrl}/api/content/article/favorite`;

		this.httpService.postHttp(url, params).subscribe(
			res => {
				this.favorite_status = 1;
			}
		);
	}


	//取消收藏
	cancel_favorite(){

		let url = `${Config.APIUrl}/api/content/article/favorite?id=${this.input_aid}`;

		this.httpService.deleteHttp(url).subscribe(
			res => {
				this.favorite_status = 0;
				this.childEvent.emit(0);
			}
		);
	}





}
